.contacts {
  @include anim-on-scroll(
    __title, transform,
    translate(-120%, 0),
    translate(0, 0), 0);
  @include anim-on-scroll(
    __form, transform,
    translate(-120%, 0),
    translate(0, 0), 300ms);
  @include anim-on-scroll(
    __address, transform,
    translate(120%, 0),
    translate(0, 0), 300ms);

  &__wrapper {
    row-gap: 40px;

    @include page-grid;
  }
}
